@import url(//db.onlinewebfonts.com/c/f4d1593471d222ddebd973210265762a?family=Pokemon);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(132, 196, 238);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  font-family: Pokemon;
  text-align: center;
  letter-spacing: 2px;
  background-color: rgb(132, 196, 238);
  min-height: 100vh;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

ul {
  list-style: none;
}
h1 {
  color: yellow;
  text-shadow: 5px 5px 5px blue;
  background-color: tomato;
  margin: 0;
}
/* * {
  background-color: rgb(132, 196, 238);
} */
.search-input {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  text-align: center;
  -webkit-align-items: center;
          align-items: center;
  border: none;
  background-color: wheat;
  border-radius: 10px;
  height: 30px;
  width: 30%;
  margin: auto;
}

.catch-release-btn {
  width: 5%;
  padding: 2%;
  cursor: pointer;
}

.pokemon-collection-item,
button,
span {
  cursor: pointer;
}

.spinner-div {
  display: none;
}
.loader {
  z-index: 1;
  -webkit-animation: loader infinite 5s linear;
          animation: loader infinite 5s linear;
  position: absolute;
  -webkit-align-content: center;
          align-content: center;
  display: -webkit-flex;
  display: flex;
  left: 20%;
  bottom: 22%;
  width: 50%;
  height: 50%;
}
.spinner-img {
  width: 100%;
  height: 100%;
}
.blur {
  -webkit-filter: blur(8px);
          filter: blur(8px);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
}

.main {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
}

.collection-div {
  display: inline-block;
}

.ul-pokemon-types {
  position: absolute;
  display: inline-block;
  max-width: -webkit-min-content;
  max-width: min-content;
}

.li-pokemon-details {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
.div-pokemon-details {
  text-align: start;
}

.ul-pokemon-types {
  /* display: flex; */
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  text-align: center;
}
.pokemon-list-by-type {
  display: block;
}

@-webkit-keyframes loader {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes loader {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

